import React from 'react'
import Link from 'gatsby-link'

import Header from '../components/header';
import Section from '../components/section';

const PolicyPage = () => (
  <div>
    <Header fixed />
    <div className="home">
      <Section id="policy-section" title="PRIVACY POLICY">
        <div className="privacy-text">
          <p>Wishberry is committed to protecting your privacy and creating a strong relationship with our users. This privacy policy explains the use of information collected via our website www.wishberryfilms.in or application or affiliated site/applications owned and operated by Wishberry (collectively, the "Site" or "We"), customer service operations.</p>
          <br/><br/>
          <h4>DEFINITIONS</h4>
          <br/>
          <p><strong>Personal Data</strong> – Any information relating to an identified or identifiable natural person such as name, address, e-mail address, or phone number, and other information that is not otherwise publicly available.
          <br/>
          <strong>Processing</strong> – Any operation or set of operations which is performed on Personal Data or on sets of Personal Data.
          <strong>Data subject</strong> – A natural person whose Personal Data is being Processed.
          <strong>Child</strong> – A natural person under 13 years of age.</p>
          <br/>
          <h4>DATA PROTECTION PRINCIPLES</h4>
          <br/>
          We promise to follow the following data protection principles:
          Processing is lawful, fair and transparent- Our processing activities have lawful grounds and we always consider your rights before processing personal data. We shall provide you with relevant information regarding processing upon request.
          Processing is limited to the purpose- Our processing activities fit the purpose for which personal data was gathered.
           Processing is done with minimal data- We only gather and process the minimal amount of personal data required for the purpose of your processing.
          Processing is limited with a time period- We will not store your personal data for longer than needed.
          We will do our best to ensure the accuracy of data.
          We will do our best to ensure the integrity and confidentiality of data.

          DATA SUBJECT RIGHTS

          The data subject has the following rights:
          1. Right to information- which means you have the right to know whether your personal data is being processed, what data is gathered, from where it is obtained and why and by whom it is processed.
          2. Right to access– which means you have the right to access the data collected from/about you at any time which shall include your right to request and obtain a copy of your personal data gathered by us.
          3. Right to rectification– which means you have the right to request rectification or erasure of your personal data that is inaccurate and/or incomplete.
          4. Right to erasure– which means in certain circumstances you can request for your personal data to be erased from our records.
          5. Right to restrict processing– which means where certain conditions apply, you have the right to restrict the processing of your personal data.
          6. Right to object to processing– which means in certain cases you have the right to object to processing of your personal data, for example in the case of direct marketing.
          7. Right to object to automated Processing– which means you have the right to object to automated processing, including profiling; and not to be subject to a decision based solely on automated processing. This right you can exercise whenever there is an outcome of the profiling that produces legal effects concerning or significantly affecting you.
          8. Right to data portability– which means you have the right to obtain your personal data in a machine-readable format or if it is feasible, as a direct transfer from one processor to another.
          9. Right to lodge a complaint– In the event that we refuse your request under the rights of access, we will provide you with a reason for the same. If you are not satisfied with the way your request has been handled you will have a right to contact us.
          10. Right for the help of supervisory authority– which means you have the right to seek help of a supervisory authority and the right for other legal remedies such as claiming damages.
          11. Right to withdraw consent– which means you have the right to withdraw any given consent for processing of your personal data.

          DATA WE GATHER

          While going through the website you may have uncertainty regarding a) What Information does Wishberry collects, b) Why does Wishberry Collect these Information. To make all these uncertainties clear, we bring to your knowledge that collecting information from you is being done on a voluntary basis. Our primary goal in collecting personal data is to provide you i.e. the user, with a unique and customized experience on our site and to deliver best services, products, and more relevant contents to you, and visitors.

          a. Collection of Information
          We may use cookies to track general, non-personal data about our users (e.g., user name, pages viewed, time spent our site, and the websites visited just before and after our site) for statistical and market research purposes. The information is collected on an aggregated and anonymous basis. We use cookies to gather information about our site users' browsing activities in order to constantly improve our site and better serve our customers. This information assists us to design and arrange our web pages in a most user-friendly manner and to continually improve our site to better meet the needs of our customers, prospective customers, and visitors. Like most internet services, we may also use log files on the server side. The data held in log files includes your IP (Internet Protocol) address, browser type, e-mail application, internet service provider, referring/exit web pages, computer platform type, date/time stamp. Wishberry may use server log data to analyze trends, administer the services offered through the site. IP addresses are not tied to any personally identifiable information. In addition, the software enabling the site may have associated log and temporary files that are stored on Wishberry's servers. These files store your account information, preference settings, system notifications as well as other data necessary to enable you to participate on the site. Your information may also exist within regularly performed server backups. The foregoing information is collected on an aggregated and anonymous basis.

          b. Methods for Collecting Information:
          Anonymous Site Visitor: If you visit our site we may record information related to your IP address, cookie information, and aggregate information on what content you access or visit. This information is collected in the background while you move around our site.
          Mailing List Member: If you choose to provide us personal data in order to subscribe to our mailing list or newsletter, we may collect information to aid delivery and preferences such as e-mail address and message performance statistics.
          Registered Site Member: If you choose to register on our site, we may track broad site usage information as well as aggregate usage of website community features such as downloads and forums. This is in addition to the personal data collected during the registration process.
          Mobile: We may use tools or third party analytical software to collect and use certain non-personal data that does not enable us to identify you. the types of non-personal data we may collect and use include, but are not limited to: (i) mobile device type and device properties; (ii) mobile device software platform and firmware; (iii) mobile phone carrier; (iv) geographical data no more accurate that metropolitan areas; (v) other non-personal data as reasonably required by us to enhance its services. We may use the collected data for purposes further developing our services and products.
          fmessage boards: If you use a message board or forum on this web site, you should be aware that any personal data you submit there can be read, collected or used by other users of these forums, and could be used to send you unsolicited messages. We are not responsible for the personal data you choose to submit in these forums.
          promotions through electronic mail: To participate in some of our promotions, you may be asked to provide an e-mail address and/or name, so that we can let you know if you won a prize. We may use e-mail addresses to fulfill requests for promotions or services. Occasionally, we share your personal data (e.g., e-mail address) with a third party sponsor, co-sponsor, or other reputable third party that provides products or services that may be of interest to you. By allowing you to send e-mail to us, we are not attempting to collect any personal data about you. We will only use personal data (such as name and e-mail address) contained in your e-mail to respond to your specific inquiry or comments in the e-mail. Although we may retain your e-mail for a period of time, we will not use any personal data contained in your e-mail for any purpose other than responding to your specific inquiry or comments. Whatever the activity may be, we will only collect information to the extent deemed reasonably necessary to fulfill your requests and our legitimate business objectives. If you no longer wish to receive our email newsletter and promotional communications, you may opt-out of receiving them by following the instructions included in any newsletter email message. We also offer an opportunity to opt-out of certain communications on our manage account web pages, or you may contact us at info@wishberry.in or use the email or physical address listed under the heading unsubscribe in newsletters/emails.
          Credit card use and payment transaction: Wishberry utilizes a credit card processor to process your payments (if any) while on our site. We share your name, email address and credit card information with these third parties to provide the service requested by you. These third parties are not permitted to use your information for their own promotional purposes. Alternatively, you have the option to use the payment options as mentioned and listed on our website. If any payment transaction occurs on our site, we collect information about such transaction, including credit card numbers or other billing information.
          Voluntary disclosure of personal data: You may choose to disclose personal data in our online community features, blogs or other public forums, directly to other users while using the site service. Please be aware that such information is public information and you should not expect privacy or confidentiality under these circumstances. Wishberry shall not be liable in any way for such disclosure of personal data.

          HOW WE USE YOUR PERSONAL DATA

          The information which we collect and store during normal use of the site is used to monitor the use of the site and to help us further develop the site. Such use does not always result in any personally identifiable data being collected or stored.

          As mentioned earlier a number of pages in this website has the option for submitting personal data and as such all these communications are not confidential. Any application, request or enquiry you make, may be passed or made available to group affiliates, some of which may be based in countries that do not have data protection laws. All group affiliates are required to comply with an internal privacy policy which requires that steps are taken to protect your personal data and for it only to be used for the purpose(s) for which it was submitted.

          By submitting your personal data through this website, you shall be consenting to it being processed in the manner described above by group affiliates.

          We assure you that we use your personal data for operational purposes only. For example, we may use your information to send you administrative communications either about your account with us or about features of our site, including any changes to this privacy policy. Whatever the purpose may be, we will only collect information to the extent reasonably necessary to fulfill your requests and our legitimate business objectives. We attempt to limit access to personal data about you to employees who we believe reasonably need to come into contact with that information to provide products or services to you or in order to do their jobs.

          We may occasionally employ other companies to perform services necessary for our internal operations, such as companies that personalize our web pages, process credit card transactions, analyze customers' interaction with our site, and process consumer surveys. We also provide postal addresses to the post office for delivery purposes in case of any purchase made by you, which will require such companies to access your personal data. These companies have agreed to securely store and maintain the personal data received from us.

          Finally, we may take your personal data and make it non-personally identifiable, either by combining it with information about other individuals (aggregating your information with information about other individuals), or by removing characteristics (e.g., your name) that make the information personally identifiable to you. This process is known as de-personalizing your information. Given the non-personal nature of this information, no restrictions apply under this privacy policy on our right to depersonalize your personal data. We may use de-personalized information in any way and share it with third parties without your consent.

          We might process your personal data for additional purposes that are not mentioned here, but are compatible with the original purpose for which the data was gathered, for which we ensure that:
          the link between purposes, context and nature of personal data is suitable for further processing;
          the further processing would not harm your interests;
          and  there would be appropriate safeguard for processing. We will inform you of any further processing and purposes.

          WHEN DO WE DISCLOSE YOUR PERSONAL DATA?

          We will disclose personal data when we believe in good faith that disclosure is required by law (e.g., court order or subpoena) or such disclosure helps enforce (i) our terms of use, contests, sweepstakes or promotions; (ii) protect your safety or security, including the safety and security of your property; or (iii) protect the safety and security of our website or third parties.

          HOW DO WE SECURE YOUR DATA?

          The security of your data is important to us and our third party vendors. We follow generally accepted industry standards to protect the personal data submitted to us, both during transmission and once we receive it. No method of transmission over the internet, or method of electronic storage, is 100% secure.

          OUR COMMITMENT TO CHILDREN'S PRIVACY

          The Site will not knowingly collect, use or retain personal data from anyone under the age of 13, for any purpose whatsoever (including internal or external marketing or promotional purposes). If you are under the age of 13, you will need your parent or guardian's consent before you can provide any personal information to us for purposes of registration and other online activities. By registering to this site we have reasons to believe that you have taken required consents from your parents/guardian.


           ACCESS, CHANGE OR DELETE DATA

          a.By parents about their children
          Once again, we would like to assure you that we do not keep any information other than what is listed above. However, parents are welcome to contact us about any personal data about their child by email. If your child's personal changes, or if you no longer desire our service, you may correct, update, or delete it by making the change by contacting us at the email address info@wishberry.in We will respond to your request to access within 30 days from the receipt of your request. Parents may request for:
          1) discontinuation of collection and use of their child's personal information;
          2) delete the child's personal information. Which can be done by contacting us at the email address provided above.

          b. By Users
          If your personal data changes, or if you are no longer desirous of our service, you may correct, update or delete it by making a request to us or by contacting us at the email address provided above. We will respond to your request to access within 30 days from the receipt of the request received by you. Therefore, while we strive to use commercially acceptable means to protect your personal data, we cannot guarantee its absolute security. If you have any questions about the security of our website, you can email us at info@wishberry.in

          INFORMATION ABOUT COOKIES

          We use cookies and/or similar technologies to analyze customer behavior, administer the website, track users’ movements, and to collect information about users. This is done in order to personalize and enhance your experience with us. A cookie is a tiny text file stored on your computer. Cookies store information that is used to help make sites work. Only we can access the cookies created by our website. You can control your cookies at the browser level by choosing to disable cookies may hinder your use of certain functions.

          We use cookies for the following purposes:
          Necessary cookies – These cookies are required for you to be able to use some important features on our website, such as logging in. These cookies don’t collect any personal data.
          Functionality cookies– These cookies provide functionality that makes using our service more convenient and makes providing more personalized features possible. For example, they might remember your name and e-mail in comment forms so you don’t have to re-enter this information next time when commenting.
          Analytics cookies– These cookies are used to track the use and performance of our website and services.
          Advertising cookies– These cookies are used to deliver advertisements that are relevant to you and to your interests. In addition, they are used to limit the number of times you see an advertisement. They are usually placed to the website by advertising networks with the website operator’s permission. These cookies remember that you have visited a website and this information is shared with other organizations such as advertisers. Often targeting or advertising cookies will be linked to site functionality provided by the other organization. You can remove cookies stored in your computer via your browser settings.

          We use google analytics to measure traffic on our website. Google has their own privacy policy which you can review Click here. If you’d like to opt out of tracking by google analytics, visit the google analytics opt-out page.

          TYPES OF COOKIES USED

          Click here to know more about the types of cookies used by us.


          COOKIE NAME
          PURPOSE
          DURATION
          DOMAIN
          CATEGORY
          DATA HELD BY COOKIE
          _ga/_gat/_gid
          Tracking user for Google Analytics
          managed by google analytics
          wishberryfilms.in
          Analytical
          Analytical



          CHANGES AND UPDATES TO THIS PRIVACY POLICY

          We reserve the right at any time to (i) change the terms of this privacy policy; (ii) change the terms of use, including eliminating or modifying any content on or feature of the website; or (iii) change/charge any fees or charges for use of the services.

          Any changes we make shall be effective immediately from the notice, which we may give either by posting the new notice on the website or via electronic mail. Your use of the services after such notice will be deemed acceptance of such changes. Be sure to review changes in the privacy policy and privacy notice periodically to ensure your familiarity with the most current version. You will always be able to tell when the version was last updated by checking the last revised date in the header of this privacy policy.

          Linking to third party sites
          To provide relevant information not found on our website, we may provide links from our websites to third party websites and we encourage our users to read third party sites privacy policies before submitting personal information. We are not responsible for the privacy practices of the content of third party sites. We hereby disclaim any representations or warranties expressed on any site other than own site.

          CONTACT INFORMATION

          If you have any questions, complaints or comments regarding our Privacy Policy Statement, you can contact us at: 102/103 Matharu Arcade, Subhash road, Vile Parle (E), Mumbai 400057, Email: info@wishberry.in
          ASSENT

          By using this site, you signify your assent to our online privacy policy. If you do not agree to this privacy policy, please do not use this site. We reserve the right to make changes to this privacy policy. Please check back from time to time to ensure that you are aware of these changes. Your continued use of this site will signify your acceptance of these changes. If you do not agree with any terms or conditions described in this privacy policy or the terms of use, we would request you to exit from the site.

        </div>
      </Section>
    </div>
  </div>
)

export default PolicyPage



